// Helper function to get role by ID
export function getRoleById(roleId: string): Role | undefined {
  return mockRoles.find(role => role.id === roleId)
}

// Helper function to get staff by role
export function getStaffByRole(roleId: string): Staff[] {
  return mockStaff.filter(staff => staff.roleId === roleId)
}

// Helper function to get active staff count
export function getActiveStaffCount(): number {
  return mockStaff.filter(staff => staff.status === 'Active').length
}

// Helper function to get role statistics
export function getRoleStatistics() {
  return {
    totalRoles: mockRoles.length,
    systemRoles: mockRoles.filter(r => r.type === 'system').length,
    customRoles: mockRoles.filter(r => r.type === 'custom').length,
    activeRoles: mockRoles.filter(r => r.isActive).length,
    totalStaff: mockStaff.length,
    activeStaff: mockStaff.filter(s => s.status === 'Active').length,
    inactiveStaff: mockStaff.filter(s => s.status === 'Inactive').length,
    suspendedStaff: mockStaff.filter(s => s.status === 'Suspended').length
  }
}
