// Permission & Role Types for Risivo CRM

export type Permission = {
  id: string
  name: string
  description: string
  module: string
  actions: PermissionAction[]
}

export type PermissionAction = 'view' | 'create' | 'edit' | 'delete' | 'manage'

export type Module = {
  id: string
  name: string
  description: string
  icon: string
  permissions: Permission[]
}

export type Role = {
  id: string
  name: string
  description: string
  type: 'system' | 'custom'
  color: string
  permissions: RolePermission[]
  createdAt: string
  updatedAt: string
  createdBy?: string
  isActive: boolean
  userCount: number
}

export type RolePermission = {
  permissionId: string
  actions: PermissionAction[]
}

export type Staff = {
  id: string
  name: string
  email: string
  phone: string
  avatar?: string
  roleId: string
  roleName: string
  roleColor: string
  status: 'Active' | 'Inactive' | 'Suspended'
  lastLogin: string
  createdAt: string
  department?: string
  jobTitle?: string
  notes?: string
}

