// Helper function to get all permissions from modules
export function getAllPermissions(): Permission[] {
  return SYSTEM_MODULES.flatMap(module => module.permissions)
}

// Helper function to check if role has permission
export function hasPermission(
  role: Role,
  permissionId: string,
  action: PermissionAction
): boolean {
  const rolePermission = role.permissions.find(p => p.permissionId === permissionId)
  if (!rolePermission) return false
  return rolePermission.actions.includes(action)
}

// Helper function to get role permission for a module
export function getModulePermissions(
  role: Role,
  moduleId: string
): PermissionAction[] {
  const module = SYSTEM_MODULES.find(m => m.id === moduleId)
  if (!module) return []

  const permissionActions: PermissionAction[] = []
  
  module.permissions.forEach(permission => {
    const rolePermission = role.permissions.find(p => p.permissionId === permission.id)
    if (rolePermission) {
      rolePermission.actions.forEach(action => {
        if (!permissionActions.includes(action)) {
          permissionActions.push(action)
        }
      })
    }
  })

  return permissionActions
}
